import React,{ Component , useState} from 'react';
/* import pugin*/
import { Row, Col ,Divider,Table , Layout, Menu, Breadcrumb,Tabs,Card,Input,Radio,Button,Modal} from 'antd';
import {tableStyle,columns} from '../component/tableComponent'
import logoApprove from '../Icon/logoApprove.png'
import moment from 'moment'
import {
    DesktopOutlined,
    PieChartOutlined,
    FileOutlined,
    TeamOutlined,
    UserOutlined,
    HomeOutlined,
    CrownTwoTone,
    SketchOutlined,
    RadarChartOutlined,
    createFromIconfontCN 
  } from "@ant-design/icons";
import '../App.css';
  
/* import API */
import APPROVEAPI from '../api/approveApi'
import Swal from 'sweetalert2';
const approveapi = new APPROVEAPI();
const { Column, ColumnGroup } = Table;
const { Header, Content, Footer, Sider } = Layout;
const IconFont = createFromIconfontCN({
    scriptUrl: '//at.alicdn.com/t/font_8d5l8fzk5b87iudi.js',
});
const { SubMenu } = Menu;
const { TabPane } = Tabs;
const styleQ = {
    fontWeight : 'bold',
    fontSize : '12px',
    marginTop : '4px',
    color : '#800080'
}
const styleA = {
    fontWeight : 'bold',
    fontSize : '12px',
    marginTop : '4px'
}
const styleDivCardSearch = {
    width: '100%', borderRadius: '5px', textAlign:'left',background:'#D8BFD8',padding:'4px 0 14px 14px'
}
const styleDivModal = {
    width: '100%', 
    borderRadius: '5px', 
    textAlign:'left',
    background:'#fff',
    padding:'4px 0 14px 14px',
    border : '1px #D8BFD8 solid'
}
const styleDivModalSecond = {
    width: '100%', 
    borderRadius: '5px', 
    textAlign:'left',
    background:'GhostWhite',
    marginTop : '16px',
    padding:'4px 0 14px 14px',
}
const styleDivCardSecond = {
    width: '100%', 
    borderRadius: '5px', 
    background:'GhostWhite',
    // padding:'4px 0 14px 14px',
    textAlign : 'center'
}
const styleButtomPrimary = {
    background :'#800080' , 
    color : 'white'
}
const styleButtomSuccess = {
    background :'green' , 
    color : 'white'
}
const styleButtomDanger = {
    background :'red' , 
    color : 'white'
}
const styleHeadQ = {
    color : '#800080',
    fontWeight : 'bold'
}
let mat = [
    {
        approveStatusId: 1,
        approveStatusName: "รออนุมัติ",
        approveTypeId: 1,
        info:{
            accountName: "นาย วงศกรณ์  แดงประดับ",
            accountNum: "810397544",
            approveUserEmail: "pandacha.s@jasmine.com",
            approveUserEmpId: "RO0015",
            custGroup: "SVIP",
            offeringId: "19232540",
            offeringName: "3BB FTTx 300M/300M (SB)",
            reqDtm: "2021-03-01 17:15:17",
            reqUserEmail: "pandacha.s@jasmine.com",
            reqUserEmpId: "3555BB"},

        reqappId: 3,
        requestDtm: "2021-03-01 17:15:17",
        requestUserEmpId: "BK0325",
        requestUserName: "นายปาณเดชา แสงสุริยา",
    }
];
let workNum = '';
class PageListApprove extends Component {
    constructor(props){
        super(props);
        this.state = {
            collapsed: true,
            accSearch  :"",
            show : {
                modalDetailAcc : false,
                modalBeforAction : false
            },
            dataSourceWaiting : [],
            dataSourceApproved : [],
            dataSourceWaitingAll : [],
            dataSourceApprovedAll : [],
            detailData : {},
            dataBeforeAction : {},
            type : 0,
            count  : {
                countSVIP :'0',
                countPVIP : '0',
                countVIP : '0'
            },
            countWaiting  : {
                countSVIP :'0',
                countPVIP : '0',
                countVIP : '0'
            },
            countAppoved  : {
                countSVIP :'0',
                countPVIP : '0',
                countVIP : '0'
            },
            selectVIP : null

        }
        this.callGetWaitingAp = this.callGetWaitingAp.bind(this)
    }
    componentDidMount() {
        this.callGetWaitingAp()
        this.callGetApprovedAp()
    }
    onCollapse = (collapsed) => {
        console.log(collapsed);
        this.setState({ collapsed });
    };
    callback = (key) => {
        console.log(key);
        if(key==='1'){
            const {countSVIP,countPVIP,countVIP} = this.state.countWaiting
            this.setState({
                count  : {
                    countSVIP : countSVIP,
                    countPVIP : countPVIP,
                    countVIP : countVIP
                }
            })
        }
        else{
            const {countSVIP,countPVIP,countVIP} = this.state.countAppoved
            this.setState({
                count  : {
                    countSVIP : countSVIP,
                    countPVIP : countPVIP,
                    countVIP : countVIP
                }
            })
        }
    }
    onChangeVip = e => {
        console.log('radio checked', e.target.value);
        const {dataSourceApprovedAll, dataSourceWaitingAll} = this.state
        this.setState({
            selectVIP : e.target.value,
            accSearch : ""
        }) 
        if(e.target.value === 1){
            const filterAp = dataSourceApprovedAll.filter(function(filter){
                return filter.info.custGroup === 'SVIP'
            })
            const filterWa = dataSourceWaitingAll.filter(function(filter){
                return filter.info.custGroup === 'SVIP'
            })
            this.setState({
                dataSourceApproved : filterAp,
                dataSourceWaiting: filterWa
            })
        }
        else if(e.target.value === 2){
            const filterAp = dataSourceApprovedAll.filter(function(filter){
                return filter.info.custGroup === 'PVIP'
            })
            const filterWa = dataSourceWaitingAll.filter(function(filter){
                return filter.info.custGroup === 'PVIP'
            })
            this.setState({
                dataSourceApproved : filterAp,
                dataSourceWaiting: filterWa
            })
        }
        else if(e.target.value === 3){
            const filterAp = dataSourceApprovedAll.filter(function(filter){
                return filter.info.custGroup === 'VIP'
            })
            const filterWa = dataSourceWaitingAll.filter(function(filter){
                return filter.info.custGroup === 'VIP'
            })
            this.setState({
                dataSourceApproved : filterAp,
                dataSourceWaiting: filterWa
            })
        }
    };
    showDetailAcc = (record , text) => {
        console.log('record', record);
        console.log('text', text);
        this.callGetDetailAcc(record.info.accountNum)
    } 
    setInputApprove = (record , text , type) => {
        this.setState({
            dataBeforeAction : record,
            type : type
        })
        this.setState(prevState =>({show:{...prevState.show,
            modalBeforAction : true
        }})) 
    }
    clickApprove = () => {
        const {reqappId} = this.state.dataBeforeAction
        let input = {
            reqappId:reqappId,
            considerId: '1',
            reason:""
        }
        this.callPostSubmit(input)
    }
    clickNoApprove = () => {
        const {reqappId} = this.state.dataBeforeAction
        let input = {
            reqappId:reqappId,
            considerId: '0',
            reason: 0
        }
        this.callPostSubmit(input)
    }
    
    setValueAccSearch = ({ target }) =>{
        this.setState({accSearch : target.value})
    }
    searchTable = () =>{
        const {accSearch,selectVIP} = this.state
        const {dataSourceApprovedAll, dataSourceWaitingAll} = this.state 
        const filterWa = dataSourceWaitingAll.filter(function(filter){
            if(selectVIP===1){
                return filter.info.accountNum === accSearch && filter.info.custGroup === 'SVIP'
            }
            else if(selectVIP===2){
                return filter.info.accountNum === accSearch && filter.info.custGroup === 'PVIP'
            }
            else if(selectVIP===3){
                return filter.info.accountNum === accSearch && filter.info.custGroup === 'VIP'
            }
            else{
                return filter.accountNum === accSearch
            }
        })
        const filterAp = dataSourceApprovedAll.filter(function(filter){
            if(selectVIP===1){
                return (filter.info.accountNum === accSearch && filter.info.custGroup === 'SVIP')
            }
            else if(selectVIP===2){
                return (filter.info.accountNum === accSearch && filter.info.custGroup === 'PVIP')
            }
            else if(selectVIP===3){
                return (filter.info.accountNum === accSearch && filter.info.custGroup === 'VIP')
            }
            else{
                return filter.info.accountNum === accSearch
            }
        })
        console.log('filterWa',filterWa)
        console.log('filterAp',filterAp)
        console.log('filterAp',accSearch)
        if(!filterWa || !filterAp){
            Swal.fire({    
                // title:  '<div style="font-size:16px;text-align :  left;background-color : #FFA07A"><span>EventID : '+data.equipment[0].eventId+'</span></div>',                    
                icon:'warning',
                text: 'ไม่พบเลขที่บัญชีที่ท่านกำลังค้นหา',            
                showConfirmButton: true,
                confirmButtonText: 'ตกลง',
                confirmButtonColor : "#FFA07A",
                showCancelButton:false,
                // cancelButtonText:'ยกเลิก',
            })
            this.setState({
                accSearch : "",
                dataSourceApproved : dataSourceApprovedAll,
                dataSourceWaiting: dataSourceWaitingAll
            })
        }
        else{
            this.setState({
                dataSourceApproved : filterAp,
                dataSourceWaiting: filterWa
            })
        }
        
    }
    // -----------------------------API---------------------------------------------
    callGetWaitingAp() {
        approveapi.getWaitingAp().then(ans => {
            console.log('getWaitingAp ==> result : ', ans);
            if(ans){
                this.setState({
                    dataSourceWaiting : ans,
                    dataSourceWaitingAll : ans,
                })
                const countSVIP = ans.filter(function(filter){
                    return filter.info.custGroup === 'SVIP'
                })
                const countPVIP = ans.filter(function(filter){
                    return filter.info.custGroup === 'PVIP'
                })
                const countVIP = ans.filter(function(filter){
                    return filter.info.custGroup === 'VIP'
                })
                this.setState({
                    countWaiting  : {
                        countSVIP : countSVIP.length,
                        countPVIP : countPVIP.length,
                        countVIP : countVIP.length
                    },
                    count  : {
                        countSVIP : countSVIP.length,
                        countPVIP : countPVIP.length,
                        countVIP : countVIP.length
                    },
                })
            }
            
            
        })
    }
    callGetApprovedAp() {
        approveapi.getApprovedAp().then(ans => {
            console.log('getApprovedAp ==> result : ', ans);
            if(ans){
                const result = ans.map((item,index) => (
                    {...item, key: index}
                ))
                this.setState({
                    dataSourceApproved : result,
                    dataSourceApprovedAll : result,
                })
                // ----------------ทดสอบเท่านั้น
                // this.setState({
                //     dataSourceWaiting : result,
                //     dataSourceWaitingAll : result,
                // })
                // ----------------ทดสอบเท่านั้น
                const countSVIP = result.filter(function(filter){
                    return filter.info.custGroup === 'SVIP'
                })
                const countPVIP = result.filter(function(filter){
                    return filter.info.custGroup === 'PVIP'
                })
                const countVIP = result.filter(function(filter){
                    return filter.info.custGroup === 'VIP'
                })
                this.setState({
                    countAppoved  : {
                        countSVIP : countSVIP.length,
                        countPVIP : countPVIP.length,
                        countVIP : countVIP.length
                    }
                })
            }
        })
    }
    callPostSubmit = (input) => {
        this.setState(prevState =>({show:{...prevState.show,
            modalBeforAction : false,
        }}))
        approveapi.postSubmit(input).then(result => {
            console.log('postSubmit ==> result : ', result);
            if(!result){
                let text = ""
                if(input.considerId==='1'){
                    text = "ระบบได้ดำเนินการอนุมัติรายการดังกล่าวสำเร็จเรียบร้อย"
                }
                else{
                    text = "ระบบได้ดำเนินการไม่อนุมัติรายการดังกล่าวสำเร็จเรียบร้อย"
                }
                Swal.fire({    
                    // title:  '<div style="font-size:16px;text-align :  left;background-color : #FFA07A"><span>EventID : '+data.equipment[0].eventId+'</span></div>',                    
                    icon:'success',
                    text: text,            
                    showConfirmButton: true,
                    confirmButtonText: 'ตกลง',
                    confirmButtonColor : "green",
                    showCancelButton:false,
                    // cancelButtonText:'ยกเลิก',
                })
                this.callGetWaitingAp()
                this.callGetApprovedAp()
            }
        })
    }
    callGetDetailAcc = (acc) => {
        let input = {accountNum : acc}
        // --------------------ทดสอบเท่านั้น
        // let input = {accountNum : '630209449'}  
        // --------------------ทดสอบเท่านั้น
        if(acc){
            approveapi.getDetailVIPAcc(input).then(ans => {
                console.log('getDetailAcc ==> result : ', ans);
                if(ans){
                    if(ans.account.accountNum){
                        this.setState({
                            detailData : ans.account
                        })
                        this.setState(prevState =>({show:{...prevState.show,
                            modalDetailAcc : true
                        }})) 
                    }
                    else{
                        Swal.fire({    
                            // title:  '<div style="font-size:16px;text-align :  left;background-color : #FFA07A"><span>EventID : '+data.equipment[0].eventId+'</span></div>',                    
                            icon:'warning',
                            text: 'ไม่พบรายละเอียดของหมายเลข '+acc+' กรุณาลองใหม่อีกครั้งภายหลังค่ะ',            
                            showConfirmButton: true,
                            confirmButtonText: 'รับทราบ',
                            confirmButtonColor : "orchid",
                            showCancelButton:false,
                            // cancelButtonText:'ยกเลิก',
                        })
                    }
                    
                }
            })
        }
    }
    render() {
        console.log('this.state',this.state)
        const { collapsed ,accSearch } = this.state;
        const{accountName , accountNum ,approveStatusId,custGroup,offering,mobileNum,reqappId,requestDtm,requestUserEmpId,requestUserName,installAddress,contactorFirstname,contactorLastname,contactorJobTitle,contactorDepartment,contactorRelationship,contactorTel,contactorTel2,contactorEmail,refCareContactTel,refCareEmail,refCareLineToken,refCareSubContactTel,refCareSubEmail2,refCareSubLineToken,refStartDate,refEndDate,verifyEmpId,verifyFirstName,verifyLastName,verifyDate,verifyBy,approveEmpId,approveFirstName,approveLastName,approveDate,approveBy,refType} = this.state.detailData
        const {countSVIP,countPVIP,countVIP} = this.state.count
        const {dataSourceWaiting , dataSourceApproved} = this.state
        
        return(
            <div style={{padding : '0'}}>   
                <Layout style={{ minHeight: "100vh" }}>
                    <Sider collapsible collapsed={collapsed} onCollapse={this.onCollapse} style={{background : '#800080'}}>
                        <div className="logo">
                            <img src={logoApprove}/>
                            {/* <span style={{fontSize : "32px"}}>A</span> */}
                            <span> pprove  v.0.0.1</span>
                        </div>
                        <Menu defaultSelectedKeys={["1"]} mode="inline" style={{background : '#800080',color : 'white'}}>
                            <Menu.Item key="1" icon={<HomeOutlined />}>
                            หน้าแรก
                            </Menu.Item>
                            {/* <Menu.Item key="2" icon={<DesktopOutlined />}>
                            Option 2
                            </Menu.Item>
                            <SubMenu key="sub2" icon={<TeamOutlined />} title="Team">
                            <Menu.Item key="6">Team 1</Menu.Item>
                            <Menu.Item key="8">Team 2</Menu.Item>
                            </SubMenu>
                            <Menu.Item key="9" icon={<FileOutlined />}>
                            Files
                            </Menu.Item> */}
                        </Menu>
                    </Sider>
                    <Layout className="site-layout">
                        <Header className="site-layout-background" style={{ padding: 0,fontWeight : 'bold',fontSize:'large' }}>ระบบจัดการอนุมัติสิทธิ์การเข้าถึงข้อมูลลูกค้ากลุ่มพิเศษ</Header>
                        <Content style={{ margin: "0 16px" }}>
                            <div>
                                <Row justify="space-between" style={{padding : '16px 0 16px 0' }}>
                                    <Col span={13}>
                                        <div style={styleDivCardSearch}>
                                            <span style={{fontWeight:'bold'}}>ค้นหาข้อมูลลูกค้ากลุ่มพิเศษ</span>
                                            <Row justify="space-between" style={styleQ}>
                                                <Col span={12}>
                                                    ค้นหา : เลขที่บัญชีลูกค้า
                                                </Col>
                                                <Col span={10} >
                                                    กรองข้อมูล : กลุ่มลูกค้าพิเศษ
                                                </Col>
                                            </Row>
                                            <Row justify="space-between" >
                                                <Col span={12}>
                                                    <Input value={accSearch} onChange={this.setValueAccSearch.bind(this)} placeholder="Basic usage" style={{ width: '70%' }}/>
                                                    <Button style={styleButtomPrimary} onClick={this.searchTable.bind(this)}>ค้นหา</Button>
                                                </Col>
                                                <Col span={10} >
                                                    <Radio.Group onChange={this.onChangeVip.bind(this)}>
                                                        <Radio value={1}>SVIP</Radio>
                                                        <Radio value={2}>PVIP</Radio>
                                                        <Radio value={3}>VIP</Radio>
                                                    </Radio.Group>
                                                </Col>
                                            </Row>
                                        </div>
                                    </Col>
                                    <Col span={10}>
                                        <Row justify="space-between" style={{textAlign : 'center'}}>
                                            <Col span={7} style={styleDivCardSecond}>
                                                <CrownTwoTone style={{color : 'yellow',fontSize : '20px'}} twoToneColor="yellow" />
                                                <Row>
                                                    <Col span = {24}>
                                                        <span className="boxSVIP">&nbsp; SVIP &nbsp;</span>
                                                    </Col>
                                                    <Col span = {24} style={{fontSize : '11px'}}>จำนวนรออนุมัติ</Col>
                                                </Row>
                                                <Row style={{fontSize:'20px'}}><Col span={24}>{countSVIP}</Col></Row>
                                            </Col>
                                            <Col span={7} style={styleDivCardSecond}>
                                                <SketchOutlined style={{color : 'blue',fontSize : '20px'}}/>
                                                <Row>
                                                    <Col span = {24}><span className="boxPVIP">&nbsp; PVIP &nbsp;</span></Col>
                                                    <Col span = {24} style={{fontSize : '11px'}}>จำนวนรออนุมัติ</Col>
                                                </Row>
                                                <Row style={{fontSize:'20px'}}><Col span={24}>{countPVIP}</Col></Row>
                                            </Col>
                                            <Col span={7} style={styleDivCardSecond}>
                                                <RadarChartOutlined style={{color : 'green',fontSize : '20px'}} />
                                                <Row>
                                                    <Col span = {24}><span className="boxVIP">&nbsp;&nbsp; VIP &nbsp;</span></Col>
                                                    <Col span = {24} style={{fontSize : '11px'}}>จำนวนรออนุมัติ</Col>
                                                </Row>
                                                <Row style={{fontSize:'20px'}}><Col span={24}>{countVIP}</Col></Row>
                                            </Col>
                                        </Row>
                                    </Col>
                                </Row>
                            </div>
                            <div
                            className="site-layout-background"
                            style={{ padding: 24, minHeight: 360 }}
                            >
                                <Tabs defaultActiveKey="1" onChange={this.callback.bind(this)}>
                                    <TabPane tab="รออนุมัติ" key="1">
                                        <Table 
                                            dataSource={dataSourceWaiting} 
                                            components={tableStyle}
                                            locale={{ emptyText: 'ไม่มีข้อมูลรออนุมัติ' }}
                                            // scroll={{ x: 1500 }
                                            // columns={columns} 
                                        >
                                            <Column title="กลุ่มลูกค้าพิเศษ" dataIndex="custGroup" key="reqappId" weight = "100" render={(text, record) => (
                                                    <div>
                                                        <span>{record.info.custGroup} </span>
                                                    </div>
                                                )}
                                            />
                                            <Column title="เลขที่บัญชีลูกค้า" dataIndex="accountNum" key="reqappId" render={(text, record) => (
                                                    <div>
                                                        <span>{record.info.accountNum} : {record.info.accountName} </span>
                                                    </div>
                                                )}
                                            />
                                            <Column title="ชื่อบัญชีลูกค้า" dataIndex="accountName" key="accountName" render={(text, record) => (
                                                    <div>
                                                        <span>{record.info.accountName} </span>
                                                    </div>
                                                )}
                                            />
                                            <Column title="offering" dataIndex="offeringId" key="reqappId"  render={(text, record) => (
                                                    <div>
                                                        <span>{record.info.offeringId} : {record.info.offeringName}</span>
                                                    </div>
                                                )}
                                            />
                                            <Column title="วันที่ขออนุมัติ" dataIndex="requestDtm" key="reqappId" render={(text, record) => (
                                                    <div>
                                                        <span>{moment(record.requestDtm).format('DD/MM/YYYY')}</span>
                                                    </div>
                                                )}
                                            />
                                            <Column title="ผู้ที่ขออนุมัติ" dataIndex="requestUserName" key="reqappId" weight= {500} />
                                            <Column title="รายละเอียดบัญชี" dataIndex="requestUserName" key="reqappId"
                                                render={(text, record) => (
                                                    <div>
                                                        <Button onClick={() => this.showDetailAcc(record,text)} style={styleButtomPrimary}>รายละเอียด</Button>
                                                    </div>
                                                )}
                                            />
                                            <Column title="จัดการข้อมุล" dataIndex="reqappId" key="reqappId"
                                                render={(text, record) => (
                                                    <div>
                                                        <Button onClick={() => this.setInputApprove(record,text,1)} style={styleButtomSuccess} block>อนุมัติ</Button>
                                                        <Button onClick={() => this.setInputApprove(record,text,2)} style={styleButtomDanger} block>ไม่อนุมัติ</Button>
                                                    </div>
                                                )}
                                            />
                                            {/* <Column title="รายละเอียดบัญชี" dataIndex="itemCode"  weight= {500} /> */}
                                            {/* <Column title="จัดการข้อมุล" dataIndex="itemCode"  weight= {500} /> */}
                                        </Table>
                                    </TabPane>
                                    <TabPane tab="อนุมัติแล้ว" key="2">
                                    <Table 
                                            dataSource={dataSourceApproved} 
                                            components={tableStyle}
                                            locale={{ emptyText: 'ไม่มีข้อมูลอนุมัติแล้ว' }}
                                            // columns={columns} 
                                        >
                                            <Column title="กลุ่มลูกค้าพิเศษ" dataIndex="custGroup"  render={(text, record) => (
                                                    <div>
                                                        <span>{record.info.custGroup} </span>
                                                    </div>
                                                )}
                                            />
                                            <Column title="เลขที่บัญชีลูกค้า" dataIndex="accountNum"  render={(text, record) => (
                                                    <div>
                                                        <span>{record.info.accountNum} </span>
                                                    </div>
                                                )}
                                            />
                                            <Column title="ชื่อบัญชีลูกค้า" dataIndex="accountName" render={(text, record) => (
                                                    <div>
                                                        <span>{record.info.accountName} </span>
                                                    </div>
                                                )}
                                            />
                                            <Column title="offering" dataIndex="offeringId"  render={(text, record) => (
                                                    <div>
                                                        <span>{record.info.offeringId} : {record.info.offeringName}</span>
                                                    </div>
                                                )}
                                            />
                                            <Column title="วันที่ขออนุมัติ" dataIndex="requestDtm"  render={(text, record) => (
                                                    <div>
                                                        <span>{moment(record.requestDtm).format('DD/MM/YYYY')}</span>
                                                    </div>
                                                )}
                                            />
                                            <Column title="ผู้ที่ขออนุมัติ" dataIndex="requestUserName"  weight= {500} />
                                            <Column title="ผู้อนุมัติ" dataIndex="approveUserName"  weight= {500} />
                                            <Column title="รายละเอียดบัญชี" dataIndex="requestUserName" 
                                                render={(text, record) => (
                                                    <div>
                                                        <Button onClick={() => this.showDetailAcc(record,text)} style={styleButtomPrimary}>รายละเอียด</Button>
                                                    </div>
                                                )}
                                            />
                                            {/* <Column title="จัดการข้อมุล" dataIndex="reqappId" 
                                                render={(text, record) => (
                                                    <div>
                                                        <Button onClick={record => this.clickApprove(record)} style={styleButtomSuccess}>อนุมัติ</Button>
                                                        <Button onClick={record => this.clickNoApprove(record)} style={styleButtomDanger}>ไม่อนุมัติ</Button>
                                                    </div>
                                                )}
                                            /> */}
                                            {/* <Column title="รายละเอียดบัญชี" dataIndex="itemCode"  weight= {500} /> */}
                                            {/* <Column title="จัดการข้อมุล" dataIndex="itemCode"  weight= {500} /> */}
                                        </Table>
                                    </TabPane>
                                </Tabs>
                            </div>
                        </Content>
                        <Footer style={{ textAlign: "center" }}>
                            หากพบปัญหาระหว่างการใช้งานระบบ กรุณาติดต่อที่ Support BCS เบอร์โทรศัพท์ 02-100-2727
                        </Footer>
                    </Layout>
                </Layout>
                <Modal
                    visible={this.state.show.modalDetailAcc}
                    closable={false}
                    footer={
                        <Button style={{background : '#D8BFD8'}} onClick={()=>{
                            this.setState(prevState =>({show:{...prevState.show,
                                modalDetailAcc : false,
                                
                            }}))}}>
                            ปิดหน้าต่างข้อมูล
                        </Button>
                    }
                    // onOk={this.handleOk}
                    // onCancel={()=>this.setState({mainPage : true})}
                    // okButtonProps={false}
                    >
                        <div>
                            <span style={styleHeadQ}>รายละเอียดข้อมูลลูกค้า</span>
                            <div style={styleDivModal}>
                                <Row justify="space-between" style={{fontWeight : 'bold'}}>
                                    <Col span={24}>
                                        <span>{accountNum} : {accountName}</span> 
                                    </Col>
                                </Row>
                                {this.state.detailData.offering && offering.map((item,Index) =>{
                                    return(
                                        <div>
                                            <Row justify="space-between" >
                                                <Col span={24}>
                                                    <span style={styleQ}> ข้อมูล Offering :</span> 
                                                    <span style={styleA}> {item.offeringId} : {item.offeringName}</span>
                                                </Col>
                                            </Row>
                                            <Row justify="space-between" >
                                                <Col span={24}>
                                                    <span style={styleQ}> ข้อมูล Promotion :</span> 
                                                    <span style={styleA}> {item.promotionId} : {item.promotion}</span>
                                                </Col>
                                            </Row>
                                            <Row justify="space-between" >
                                                <Col span={24}>
                                                    <span style={styleQ}> สถานที่ติดตั้ง :</span> 
                                                    <span style={styleA}> {item.installAddress}</span>
                                                </Col>
                                            </Row>
                                        </div>
                                    )})}
                                
                                <Row justify="space-between" >
                                    <Col span={24}>
                                        <span style={styleQ}>  เบอร์โทรหลัก :</span> 
                                        <span style={styleA}> {mobileNum}</span>
                                    </Col>
                                </Row>
                            </div>
                            <div style={styleDivModalSecond}>
                                <Row justify="space-between">
                                    <Col span={19}>
                                        <Row justify="space-between">
                                            <Col span={24}><span style={{fontWeight:'bold'}}>ข้อมูลที่ขออนุมัติ (เจ้าของบัญชี)</span> </Col>
                                            <Col span={24}>
                                                <span style={styleQ}>ชื่อ-นามสกุล : </span> 
                                                <span style={styleA}>{accountName}</span>  
                                            </Col>
                                        </Row>
                                        
                                    </Col>
                                    <Col span={3} style={{textAlign : 'center', fontSize : '11px'}}>
                                        {refType==='SVIP' && <div>
                                            <CrownTwoTone style={{color : 'yellow',fontSize : '20px'}} twoToneColor="yellow" />
                                            <Row>
                                                <Col span = {24}>
                                                    <span className="boxSVIP">&nbsp; SVIP &nbsp;</span>
                                                </Col>
                                            </Row>
                                        </div> }
                                        {refType==='PVIP' && <div>
                                            <SketchOutlined style={{color : 'blue',fontSize : '20px'}}/>
                                            <Row>
                                                <Col span = {24}>
                                                    <span className="boxPVIP">&nbsp; PVIP &nbsp;</span>
                                                </Col>
                                            </Row>
                                        </div> }
                                        {refType==='VIP' && <div>
                                            <RadarChartOutlined style={{color : 'green',fontSize : '20px'}} />
                                            <Row>
                                                <Col span = {24}>
                                                    <span className="boxVIP">&nbsp; VIP &nbsp;</span>
                                                </Col>
                                            </Row>
                                        </div> }
                                    </Col>
                                </Row>
                                
                                {/* <Row justify="space-between">
                                    <Col span={24}>
                                        <span style={styleQ}>ชื่อ-นามสกุล : </span> 
                                        <span style={styleA}>{accountName}</span> 
                                    </Col>
                                </Row> */}
                                <Row justify="space-between" >
                                    <Col span={24}>
                                        <span style={styleQ}>ตำแหน่ง : </span> 
                                        <span style={styleA}>xxxxxxx</span> 
                                    </Col>
                                </Row>
                                <Row justify="space-between" >
                                    <Col span={24}>
                                        <span style={styleQ}>หน่วยงาน : </span> 
                                        <span style={styleA}>xxxxxxx</span> 
                                    </Col>
                                </Row>
                                <Row justify="space-between" >
                                    <Col span={24}>
                                        <span style={styleQ}>เบอร์โทรติดต่อ1 : </span> 
                                        <span style={styleA}>xxxxxxxxxxx</span> 
                                    </Col>
                                </Row>
                                <Row justify="space-between" >
                                    <Col span={24}>
                                        <span style={styleQ}>เบอร์โทรติดต่อ2 : </span> 
                                        <span style={styleA}>xxxxxxxxxxx</span> 
                                    </Col>
                                </Row>
                                <Row justify="space-between" >
                                    <Col span={24}>
                                        <span style={styleQ}>E-mail : </span> 
                                        <span style={styleA}>xxxxxxxxxxx</span> 
                                    </Col>
                                </Row>
                                <Row justify="space-between" >
                                    <Col span={12}>
                                        <span style={styleQ}>วันที่เริ่มต้น : </span> 
                                        <span style={styleA}> {moment(refStartDate).format('DD/MM/YYYY')}</span> 
                                    </Col>
                                    <Col span={12} >
                                        <span style={styleQ}>วันที่สิ้นสุด : </span> 
                                        <span style={styleA}> {moment(refEndDate).format('DD/MM/YYYY')}</span> 
                                    </Col>
                                </Row>
                                <Row justify="space-between" >
                                    <Col span={24}>
                                        <span style={styleQ}>รายละเอียดเพิ่มเติม :</span> 
                                        <span style={styleA}>xxxxxxxxxxxxxxxxxxxxxxxxxx</span> 
                                    </Col>
                                </Row>
                            </div>
                            <div style={styleDivModalSecond}>
                                <span style={{fontWeight:'bold'}}>ผู้รับเรื่อง (ตัวแทนบัญชี)</span>
                                <Row justify="space-between">
                                    <Col span={24}>
                                        <span style={styleQ}>ชื่อ-นามสกุล : </span> 
                                        <span style={styleA}>{contactorFirstname} {contactorLastname}</span> 
                                    </Col>
                                </Row>
                                <Row justify="space-between" >
                                    <Col span={24}>
                                        <span style={styleQ}>ตำแหน่ง : </span> 
                                        <span style={styleA}>{contactorJobTitle}</span> 
                                    </Col>
                                </Row>
                                <Row justify="space-between" >
                                    <Col span={24}>
                                        <span style={styleQ}>หน่วยงาน : </span> 
                                        <span style={styleA}>{contactorDepartment}</span> 
                                    </Col>
                                </Row>
                                <Row justify="space-between" >
                                    <Col span={24}>
                                        <span style={styleQ}>ความสัมพันธ์ต่อเจ้าของบัญชี : </span> 
                                        <span style={styleA}>{contactorRelationship}</span> 
                                    </Col>
                                </Row>
                                <Row justify="space-between" >
                                    <Col span={24}>
                                        <span style={styleQ}>เบอร์โทรติดต่อ1 : </span> 
                                        <span style={styleA}>{contactorTel}</span> 
                                    </Col>
                                </Row>
                                <Row justify="space-between" >
                                    <Col span={24}>
                                        <span style={styleQ}>เบอร์โทรติดต่อ2 : </span> 
                                        <span style={styleA}>{contactorTel2}</span> 
                                    </Col>
                                </Row>
                                <Row justify="space-between" >
                                    <Col span={24}>
                                        <span style={styleQ}>E-mail : </span> 
                                        <span style={styleA}>{contactorEmail}</span> 
                                    </Col>
                                </Row>
                            </div>
                            <div style={styleDivModalSecond}>
                                <span style={{fontWeight:'bold'}}>พนักงานดูแลบัญชี (หลัก)</span>
                                <Row justify="space-between">
                                    <Col span={24}>
                                        <span style={styleQ}>ชื่อ-นามสกุล : </span> 
                                        <span style={styleA}>{requestUserName}</span> 
                                    </Col>
                                </Row>
                                <Row justify="space-between" >
                                    <Col span={24}>
                                        <span style={styleQ}>ตำแหน่ง : </span> 
                                        <span style={styleA}>xxxxxxx</span> 
                                    </Col>
                                </Row>
                                <Row justify="space-between" >
                                    <Col span={24}>
                                        <span style={styleQ}>เบอร์โทรศัพท์พนักงาน : </span> 
                                        <span style={styleA}>{refCareContactTel}</span> 
                                    </Col>
                                </Row>
                                <Row justify="space-between" >
                                    <Col span={24}>
                                        <span style={styleQ}>E-mail : </span> 
                                        <span style={styleA}>{refCareEmail}</span> 
                                    </Col>
                                </Row>
                                <Row justify="space-between" >
                                    <Col span={24}>
                                        <span style={styleQ}>Token Line Notidy : </span> 
                                        <span style={styleA}>{refCareLineToken}</span> 
                                    </Col>
                                </Row>
                            </div>
                            <div style={styleDivModalSecond}>
                                <span style={{fontWeight:'bold'}}>พนักงานดูแลบัญชี (สำรอง)</span>
                                <Row justify="space-between">
                                    <Col span={24}>
                                        <span style={styleQ}>ชื่อ-นามสกุล : </span> 
                                        <span style={styleA}>{requestUserName}</span> 
                                    </Col>
                                </Row>
                                <Row justify="space-between" >
                                    <Col span={24}>
                                        <span style={styleQ}>ตำแหน่ง : </span> 
                                        <span style={styleA}>xxxxxxx</span> 
                                    </Col>
                                </Row>
                                <Row justify="space-between" >
                                    <Col span={24}>
                                        <span style={styleQ}>เบอร์โทรศัพท์พนักงาน : </span> 
                                        <span style={styleA}>{refCareSubContactTel}</span> 
                                    </Col>
                                </Row>
                                <Row justify="space-between" >
                                    <Col span={24}>
                                        <span style={styleQ}>E-mail : </span> 
                                        <span style={styleA}>{refCareSubEmail2}</span> 
                                    </Col>
                                </Row>
                                <Row justify="space-between" >
                                    <Col span={24}>
                                        <span style={styleQ}>Token Line Notidy : </span> 
                                        <span style={styleA}>{refCareSubLineToken}</span> 
                                    </Col>
                                </Row>
                            </div>
                            <div style={styleDivModalSecond}>
                                <span style={{fontWeight:'bold'}}>ผู้ตรวจสอบ</span>
                                <Row justify="space-between">
                                    <Col span={24}>
                                        <span style={styleQ}>ชื่อ-นามสกุล : </span> 
                                        <span style={styleA}>{verifyEmpId} : {verifyFirstName} {verifyLastName}</span> 
                                    </Col>
                                </Row>
                                <Row justify="space-between" >
                                    <Col span={24}>
                                        <span style={styleQ}>วันที่ตรวจสอบ : </span> 
                                        {verifyDate!==''&& <span style={styleA}>{moment(verifyDate).format('DD/MM/YYYY')}</span> }
                                    </Col>
                                </Row>
                                <Row justify="space-between" >
                                    <Col span={24}>
                                        <span style={styleQ}>E-mail : </span> 
                                        <span style={styleA}>{verifyBy}</span> 
                                    </Col>
                                </Row>
                            </div>
                            <div style={styleDivModalSecond}>
                                <span style={{fontWeight:'bold'}}>ผู้อนุมัติ</span>
                                <Row justify="space-between">
                                    <Col span={24}>
                                        <span style={styleQ}>ชื่อ-นามสกุล : </span> 
                                        <span style={styleA}>{approveEmpId} : {approveFirstName} {approveLastName}</span> 
                                    </Col>
                                </Row>
                                <Row justify="space-between" >
                                    <Col span={24}>
                                        <span style={styleQ}>วันที่อนุมัติ : </span> 
                                        {approveDate!==''&&<span style={styleA}>{moment(approveDate).format('DD/MM/YYYY')}</span>}
                                    </Col>
                                </Row>
                                <Row justify="space-between" >
                                    <Col span={24}>
                                        <span style={styleQ}>E-mail : </span> 
                                        <span style={styleA}>{approveBy}</span> 
                                    </Col>
                                </Row>
                            </div>
                        </div>
                </Modal>
                <Modal
                    visible={this.state.show.modalBeforAction}
                    // footer={null}
                    closable={false}
                    title={<span style={{fontWeight : 'bold'}}>กรุณาตรวจสอบข้อมูลบัญชีลูกค้า</span>}
                    footer={
                        <div>
                            {this.state.type===1 &&<Button size="smaill" style={{background : 'green',color:'white'}} onClick={this.clickApprove.bind(this)}>
                                ยืนยันอนุมัติ
                            </Button>}
                            {this.state.type===2 &&<Button size="smaill" style={{background : 'red',color:'white'}} onClick={this.clickNoApprove.bind(this)}>
                                ยืนยันไม่อนุมัติ
                            </Button>}
                            <Button size="smaill" style={{background : '#D8BFD8'}} onClick={()=>{
                                this.setState(prevState =>({show:{...prevState.show,
                                    modalBeforAction : false,
                                    
                                }}))}}>
                                ปิดหน้าต่าง
                            </Button>
                        </div>
                    }
                        
                    
                    // onOk={this.handleOk}
                    // onCancel={()=>this.setState({mainPage : true})}
                    // okButtonProps={false}
                    >
                        {this.state.dataBeforeAction.info!==undefined &&<div>
                            <Row justify="space-between">
                                <Col span={24}>
                                    <span  style={{fontWeight : 'bold' , color : '#800080'}}>บัญชีลูกค้า : </span>
                                    <span style={{fontWeight : 'bold'}}>{this.state.dataBeforeAction.info.accountNum} {this.state.dataBeforeAction.info.accountName} </span>
                                </Col>
                            </Row>
                            <Row justify="space-between">
                                <Col span={24}>
                                    <span style={{fontWeight : 'bold' , color : '#800080'}}>Offering : </span>
                                    <span style={{fontWeight : 'bold'}}>{this.state.dataBeforeAction.info.offeringId} : {this.state.dataBeforeAction.info.offeringName}</span>
                                </Col>
                            </Row>
                        </div>}
                </Modal>
            </div>
        );
    }
}

export default PageListApprove;
